<template>
  <div style="max-width: 100%;box-sizing: border-box;" class="bindInfo">
    <myHeader :isBack="false" title="绑定信息"></myHeader>
    <div class="loginForm">
      <mt-field placeholder="请输入姓名" v-model="formData.name"></mt-field>
      <mt-field placeholder="请输入身份证号码" v-model="formData.personID"></mt-field>
      <mt-field placeholder="请输入手机号码" v-model="formData.tel"></mt-field>
      <mt-field placeholder="请输入验证码" v-model="formData.invitationCode">
        <mt-button size="small"  :style="{background:showBgColor,color:isWhite}" @click="getCodeData()">{{codeTitle}}</mt-button>
        <!--<div class="getCode" @click="getCodeData()">{{codeTitle}}</div>-->
      </mt-field>
      <div class="takeCare-buy">
        <img @click="agreeReadHandler" v-if="isAgreeRead" class="select-btn" src="../../assets/select-icon.png">
        <img @click="agreeReadHandler" v-else class="select-btn" src="../../assets/noSelect-icon.png">
        本人已阅读并同意<router-link to="/openText" class="mark-text">《正邦养户服务号使用协议》</router-link>
      </div>
      <div class="submit-bth" @click="bindData()">立即绑定</div>
    </div>
    <div class="flex">
      <div class="help" ><span @click="gotoOpenHelp()">使用帮助</span></div>
      <div class="notice" ><span @click="gotoOpenAccout()">新养户开户</span></div>
    </div>
  </div>
</template>

<script>
import myHeader from "@/components/Header";
import { bindUser, getCode } from "@/api/bindInfo";
import { submitValidator } from "@/utils/validator";
import { Toast } from "mint-ui";
export default {
  name: "bindInfo",
  components: {
    myHeader
  },
  data() {
    return {
      formData: {
        name: "",
        personID: "",
        tel: "",
        invitationCode: ""
      },
      isAgreeRead: false,
      codeTitle: "验证码",
      isSend: true,
      showBgColor:'#409EFF',
      isWhite:'white'
    };
  },
  methods: {
    agreeReadHandler() {
      this.isAgreeRead = !this.isAgreeRead;
    },
    bindData() {
      const needCheckList = [
        {
          val: this.formData.name,
          rule: ["required", "standard"],
          type: "ChineseName"
        },

        {
          val: this.formData.personID,
          rule: ["required", "standard"],
          type: "IDCard"
        },
        {
          val: this.formData.tel,
          rule: ["required", "standard"],
          type: "tel"
        }, {
          val: this.formData.invitationCode,
          rule: ['required'],
          getTipText: '手机验证码'
        }, {
          val: this.isAgreeRead,
          rule: ['requiredChecked'],
          type: 'agreeAppointment'
        }
      ];
      if (!submitValidator(needCheckList)) return;
      let data = {
        identity: this.formData.personID,
        invitationCode: this.formData.invitationCode,
        custName: this.formData.name,
        telNum: this.formData.tel
      };
      bindUser(data)
        .then(res => {
          if (res.status === 200) {
            this.$router.push("/business");
          } else {
            Toast({
              message: res.message[0].message,
              duration: 3000
            });
          }
        })
        .catch(err => {
          Toast({
            message: err,
            duration: 3000
          });
        });
    },
    gotoOpenAccout() {
      this.$router.push("/register");
    },
    gotoOpenHelp() {
      this.$router.push("/helpList");
    },
    getCodeData() {
      let  _this = this;
      if (_this.formData.tel !== "") {
        if (/^[1][3,4,5,7,8,9][0-9]{9}$/.test(this.formData.tel)) {
          this.showBgColor="grey";
          if (_this.isSend) {
            var InterValObj; //timer变量，控制时间
            var count = 60; //间隔函数，1秒执行
            var curCount; //当前剩余秒数
            curCount = count;
            this.codeTitle = curCount + "秒";
            InterValObj = window.setInterval(SetRemainTime, 1000);
            _this.isSend = false;
            this.showBgColor="grey";
            getCode(_this.formData.tel)
              .then(res => {
                _this.isSend = false;
              })
              .catch(err => {
                window.clearInterval(InterValObj);
                _this.codeTitle = "重新发送";
                _this.showBgColor="#409EFF";
                _this.isSend = true;
                _this.formData.invitationCode = "";
                Toast({
                  message: "发送失败",
                  duration: 2000
                });
              });
          }
        } else {
          Toast({
            message: "手机号码错误！",
            duration: 2000
          });
        }
      } else {
        Toast({
          message: "手机号码不能为空！",
          duration: 2000
        });
      }
      function SetRemainTime() {
        //计时器
        if (curCount == 0) {
          window.clearInterval(InterValObj);
          _this.isSend = true;
          _this.codeTitle = "重新发送";
          _this.showBgColor="#409EFF";
          _this.formData.invitationCode = "";
        } else {
          _this.isSend = false;
          curCount--;
          _this.codeTitle = curCount + "秒";
        }
      }
    }
  }
};
</script>

<style scoped lang="scss">
.bindInfo {
  .loginForm {
    margin-top: 0.94rem;
    padding: 0 0.3rem;
    .mint-cell {
      border-bottom: 1px solid #bdc4ce;
    }
    .mint-field {
      height: 0.64rem;
      .mint-cell-value input[type="text"] {
        text-align: left !important;
        &::-webkit-input-placeholder {
          text-align: left;
        }
        .submit {
          width: 80%;
        }
      }
    }
  }
  .takeCare-buy {
    font-size: .14rem;
    margin: .3rem 0;
    text-align: center;

    .select-btn {
      width: .16rem;
      height: .16rem;
      vertical-align: text-bottom;
    }

    .mark-text {
      color: #FF6A1D;
    }
  }
  .getCode {
    width: auto;
    position: relative;
    height: 0.3rem;
    color: #000000;
    font-size: 0.17rem;
    display: flex;
    align-content: center;
    align-items: center;
    padding: 0.05rem;
    margin-left: 0.05rem;
    &::before {
      content: "";
      height: 0.16rem;
      width: 1px;
      background: #bdc4ce;
      left: -0.08rem;
      position: absolute;
    }
  }

}
.submit-bth {
  font-size: 0.16rem;
  margin: 0.4rem 0.2rem 0.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    90deg,
    rgba(248, 155, 49, 1) 0%,
    rgba(255, 106, 29, 1) 100%
  );
  opacity: 1;
  border-radius: 0.3rem;
  color: #fff;
  height: 0.4rem;
}
.flex{
  justify-content: space-between;
}
.notice ,.help {
  font-size: 0.14rem;
  height: 0.45rem;
  padding: 0 0.1rem;
  background: #ffffff;
  color: rgb(64, 158, 255);
  span{
    height: .45rem;
    line-height: .45rem;
  }
}

</style>



